body {
  font-family: 'DM Sans', sans-serif;
  font-family: 'Titan One', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  display: block;
}

button {
  border: none;
  font-family: 'DM Sans';
}
