p {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 186%;
  text-align: center;
  letter-spacing: 0.04em;
  color: #000000;
  @media screen and (max-width: 767px) {
    font-size: 12px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}
h1 {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 186%;
  text-align: center;
  color: #000000;
  @media screen and (max-width: 767px) {
    font-size: 14px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 16px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}
.opinions {
  background-color: #e5e5e5;
}
.opinions__text {
  background-color: #fafafa;
  @media screen and (max-width: 767px) {
    width: 280px;
    height: 308px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 620px;
    height: 164px;
  }
  @media screen and (min-width: 1280px) {
    width: 926px;
    height: 138px;
  }
}
.elipse {
  width: 4px;
  height: 4px;
  fill: #c7133e;
}
.elipse-off {
  width: 12px;
  height: 12px;
  fill: #d9d9d9;
}
.home {
  width: 12px;
  height: 12px;
  fill: #d31342;
}
.quote {
  width: 54px;
  height: 40px;
  fill: #e1e1e1;
}
